"use client";
import React, { useState } from "react";
import Loader from "@/components/Loader";
import { useTranslation } from "react-i18next";

const Index = () => {
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [status, setStatus] = useState("");
  const [loading, setLoading] = useState(false); // –°–æ—Å—Ç–æ—è–Ω–∏–µ –∑–∞–≥—Ä—É–∑–∫–∏
  const [formVisible, setFormVisible] = useState(false); // –°–æ—Å—Ç–æ—è–Ω–∏–µ –≤–∏–¥–∏–º–æ—Å—Ç–∏ —Ñ–æ—Ä–º—ã
  const { t } = useTranslation();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true); // –ü–æ–∫–∞–∑–∞—Ç—å Loader

    const formData = {
      email: email,
      message: message,
    };

    try {
      const response = await fetch(
        "https://bonusnumber1.com/api/sent_email_support.php",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(formData),
        }
      );

      setTimeout(() => {
        setLoading(false); // –°–∫—Ä—ã—Ç—å Loader —á–µ—Ä–µ–∑ 1 —Å–µ–∫—É–Ω–¥—É
      }, 1000);

      if (response.ok) {
        setStatus(t("The message was sent hastily!"));
        setTimeout(() => {
          setFormVisible(false); // –ó–∞–∫—Ä—ã–≤–∞–µ–º —Ñ–æ—Ä–º—É —á–µ—Ä–µ–∑ 1 —Å–µ–∫—É–Ω–¥—É –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏
        }, 1000);
      } else {
        setStatus(t("Error"));
      }
    } catch (error) {
      setStatus(t("Error"));
      setLoading(false); // –°–∫—Ä—ã—Ç—å Loader –≤ —Å–ª—É—á–∞–µ –æ—à–∏–±–∫–∏
    }
  };

  const toggleForm = () => {
    setFormVisible(!formVisible); // –ü–µ—Ä–µ–∫–ª—é—á–∏—Ç—å –≤–∏–¥–∏–º–æ—Å—Ç—å —Ñ–æ—Ä–º—ã
  };

  return (
    <div>
      <button onClick={toggleForm} className="btn btn-support">
        {t("Support")}
      </button>

      {formVisible && (
        <div className="form-support">
          {loading && (
            <div className="loader-overlay">
              <Loader /> {/* –õ–æ–∞–¥–µ—Ä –ø–æ–≤–µ—Ä—Ö —Ñ–æ—Ä–º—ã */}
            </div>
          )}
          <div className={`formS ${loading ? "blurred" : ""}`}>
            <button onClick={toggleForm} className="close-btn">X</button> {/* –ö–Ω–æ–ø–∫–∞ –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è —Ñ–æ—Ä–º—ã */}
            <h1>{t("Contact Us")}</h1>
            <form onSubmit={handleSubmit}>
              <div>
                <label>{t("Email")}:</label>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
              <div>
                <label>{t("Message")}:</label>
                <textarea
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  required
                />
              </div>
              <button className="btn btn-orange mt-4" type="submit">
                {t("Send")}
              </button>
            </form>
            {status && <p>{status}</p>}
          </div>
        </div>
      )}
    </div>
  );
};

export default Index;
