'use client';

import { useEffect, useState } from 'react';

export default function MainWrapper({ children }: { children: React.ReactNode }) {
  const [languageClass, setLanguageClass] = useState(() => {
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø—Ä–∏ –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–µ
    return typeof window !== 'undefined' ? localStorage.getItem('i18nextLng') || '' : '';
  });

  useEffect(() => {
    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ –∏–∑–º–µ–Ω–µ–Ω–∏–π –≤ localStorage –∫–∞–∂–¥—ã–µ 500 –º—Å
    const intervalId = setInterval(() => {
      const storedLanguage = localStorage.getItem('i18nextLng');
      if (storedLanguage !== languageClass) {
        setLanguageClass(storedLanguage || '');
      }
    }, 500); // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∫–∞–∂–¥—ã–µ –ø–æ–ª—Å–µ–∫—É–Ω–¥—ã

    // –û—á–∏—Å—Ç–∫–∞ –∏–Ω—Ç–µ—Ä–≤–∞–ª–∞ –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
    return () => {
      clearInterval(intervalId);
    };
  }, [languageClass]);

  return <main className={languageClass}>{children}</main>;
}
